export class CompanyItem{
    id:string='';
    taxpayerId:string = '';
    itemId:string = '';
    amount:number = 0;
    billingYear:number = 0;
    companyStatus:string = '';
    firstname: string = '';
    lastname:string = '';
    surname: string = '';
    itemName: string = '';

    isLoading:boolean = false;
    eventType:string = '';
}


